    <div class="gayTxt">
      理赔申请已提交至保险公司审核，请耐心等待，您可在<span class="blk">【我的理赔】</span>查看进度。<br/>
      初步理赔金额为系统自动计算出的理赔金额，最终请以保险公司审核的理赔金额为准。
    </div>
