</template>

<script>
  import { mapGetters, mapMutations } from 'vuex'
export default {
  data () {
    return {}
  },
  methods:{
    confirm(){
      this.SET_CLAIM_TAB_CUR(1)
      this.SET_CLAIM_TAB_CUR_STATE('apply')
      this.$router.push({
        path: '/claimList'
      })
    },
    ...mapMutations([ 'SET_CLAIM_TAB_CUR', 'SET_CLAIM_TAB_CUR_STATE'])
  },
  computed: {
    res () {
      return this.$route.params.data
    },
    ...mapGetters(['userInfo'])
  }
}
</script>

